.message {
  margin-bottom: 60px;

  @media (min-width: $desktop-width) {
    grid-row: 2/2;
  }

  &__fieldset {
    border: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 30px;

    &:nth-child(3) {
      margin-bottom: 0;
    }
  }

  &__title {
    font-family: 'PT Sans Narrow', sans-serif;
    font-size: 28px;
    line-height: 36px;
    padding: 0;
    margin-bottom: 18px;
  }

  &__textarea {
    width: 100%;
    min-height: 160px;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    resize: none;
    padding-top: 9px;
  }

  &__label {
    position: relative;
    margin-bottom: 29px;

    &:last-child {
      margin-bottom: 2px;
    }

    &-contacts {
      display: flex;
      flex-direction: column;
      row-gap: 8px;
      padding-bottom: 18px;
      padding-top: 5px;

      &:last-child {
        padding-bottom: 0;
      }

      @media (min-width: $desktop-width) {
        // flex-direction: row;
        // white-space: nowrap;
        display: grid;
        grid-template-columns: 95px 1fr;
        align-items: center;
        margin-top: -6px;
      }
    }
  }

  &__checkbox {
    appearance: none;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    cursor: pointer;
    margin-right: 26px;

    &:hover::before {
      border: 1px solid rgba(0, 0, 0, 0.35);
    }

    &:active::before {
      border: 2px solid $medium_light-blue;
    }

    &:checked + .message__checkbox-style::before {
      content: "";
      position: absolute;
      top: 7px;
      left: 5px;
      width: 15px;
      height: 11px;
      background-image: url("../img/check_mark.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      cursor: pointer;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 24px;
      height: 24px;
      border: 1px solid rgba(0, 0, 0, 0.15);
      border-radius: 4px;
    }
  }

  &__text {
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    height: 32px;
    width: 100%;
    cursor: pointer;
    outline: none;

    &:hover {
      border: 1px solid rgba(0, 0, 0, 0.35);
    }

    &:active,
    &:focus {
      border: 2px solid $medium_light-blue;
      color: $basic_black;
      border-radius: 4px;
    }
  }

  &__subtext {
    font-size: 14px;
    line-height: 18px;
    color: $basic_gray;
    margin-top: 7px;
    margin-bottom: 64px;

    @media (min-width: $desktop-width) {
      margin-left: 95px;
      margin-bottom: 60px;
    }
  }

  &__button {
    width: 100%;
    font-size: 20px;
    line-height: 26px;
    padding: 11px 29px;

    &:active path {
      fill: rgba(255, 255, 255, 0.6);
    }

    @media (min-width: $desktop-width) {
      width: auto;
      padding: 11px 22px;
      margin-left: 242px;
    }
  }
}

.message__text[type="text"],
.message__textarea {
  font-family: 'PT Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  // color: $basic_gray;
  padding-left: 11px;
}
