.page {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: 400;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: auto;
  padding: 0 20px;
  margin: 0 auto;

  // @media (min-width: $tablet-width) {
  //   padding: 0 30px;
  // }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.button {
  background: $medium_dark-blue;
  color: $basic_white;
  border-radius: 4px;
  text-align: center;
  border: none;
  outline: none;
  padding: 6px 29px;
  display: block;
  font-family: 'PT Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: inherit;
  line-height: inherit;
  margin: 0 auto;
  cursor: pointer;

  @media (min-width: $desktop-width) {
    margin-bottom: 5px;
  }

  &:hover {
    background-color: $medium_light-blue;
  }

  &:active {
    background-color: $special_dark-blue;
    color: rgba(255, 255, 255, 0.6);
  }
}
