.page {
  font-family: 'PT Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: $basic_black;

  &__logo:hover path {
    fill: $medium_light-blue;
    cursor: pointer;
  }

  &__logo:active path {
    fill: $special_dark-blue;
    cursor: pointer;
  }

  &__header {
    margin-bottom: 59px;

    @media (min-width: $desktop-width) {
      margin-bottom: 48px;
    }
  }

  &__main {
    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: 1fr 300px;
      column-gap: 40px;
    }
  }
}

.page__body {
  min-width: 375px;
  margin: 0 auto;

  // @media (min-width: $tablet-width) {
  //   min-width: 768px;
  // }

  @media (min-width: $desktop-width) {
    max-width: 1200px;
    padding: 0 100px;
  }
}

.page__nav {
  padding: 34px 25px 0;

  @media (min-width: $desktop-width) {
    // display: flex;
    // justify-content: space-between;
    display: grid;
    grid-template-columns: 1fr 1fr auto;
    align-items: center;
    padding: 84px 0 0;
  }
}

.page__menu {
  margin: 10px 0 15px;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;

  &-item {
    display: contents;
    margin-bottom: 15px;
  }

  &-link {
    color: $medium_dark-blue;
    text-decoration: underline;

    &:hover {
      color: $medium_light-blue;
      text-decoration: none;
    }

    &:active {
      color: $special_dark-blue;
    }

    @media (min-width: $desktop-width) {
      margin-right: 19px;
    }
  }

  @media (min-width: $desktop-width) {
    justify-content: flex-end;

  }
}
